import { useEffect,useState } from 'react'
import axios from 'axios'

export default function useFetchDiaries(){
  const [diaries, setDiaries] = useState([])

  useEffect(() => {
    fetch()
  }, [])

  async function fetch(){
    axios.get('/diary_collection_file.json')
    .then(data=>{
      if(data.status == 200){
        let diaries_arr = [...data.data]
        console.log('获取日记：', diaries_arr)
        setDiaries(diaries_arr)
      }
    })
  }

  return diaries
}