import {combineReducers} from 'redux'

const fetchArticleDetails = (
    state={
      loading: false, 
      articleObj: {}, 
      err: false
    }, 
    action
  ) => {
  switch(action.type){
    case 'fetch-article-start': 
      console.log('请求开始')
      return {...state, loading: true};
    case 'fetch-article-success': 
      console.log('请求成功')
      return {...state, loading: false, articleObj: action.res};
    case 'fetch-article-failed':
      console.log('请求失败')
      return {...state, loading: false, err: true};
    default: return state
  }
}

export const  articleDetailsReducer = combineReducers({
  fetchArticleDetails
})
