import React,{useState, useEffect} from 'react'

import './LazyList.css'

export default function LazyListHoc(allData, _size){
  const [lazyData, setLazyData] = useState([])
  const [size, setSize] = useState(_size)
  const [step, setStep] = useState(1)
  
  useEffect(() => {
    setStep(1)
    setLazyData(allData.slice(0,1 * size))
    //
  }, [JSON.stringify(allData)])
  
  function lazyLoad(){
    //console.log('高阶组件接收到的 alldata:', allData)
    let lazyData = allData.slice(0,(step+1) * size)
    setLazyData(lazyData)
    setStep((step)=>step+1)
  }

  return (ListCom)=>
    <div>
      <ListCom list={lazyData}/>
      {
        lazyData.length===allData.length ?
        null:
        <div onClick={lazyLoad} id='loadmore-button'>加载更多</div>
      }
    </div>
}
