import {useEffect, useState} from 'react'
import axios from 'axios'
import {commonArr} from '../../utilities'

export function useFetchArticles(article_tags){
  const [articles, setArticles] = useState([])

  useEffect(() => {
    fetchArticles()
  }, [article_tags.length])

  async function fetchArticles(){
    axios.get('/articles_collection_json_file.json')
    .then(data=>{
      if(data.status==200){
        let allArticles = [...data.data]
        //console.log(allArticles)
       
        if(article_tags.length) setArticles(allArticles.filter(
          (elm)=>{
            return commonArr(elm.tags, article_tags).length
          })
        )
        else setArticles(allArticles)
      }
    })
  }

  return articles
}