import React,{useMemo}  from 'react'

import './recent-act-list.css'
import './column-head.css'

import useFetchRctAct from '../custmon-hooks/useFetchRctAct'

import LazyListHoc from './LazyList'

import {Timeline, TimelineEvent} from 'react-event-timeline'

import 晴天 from '../../assets/晴天.png'
import 彩虹 from '../../assets/彩虹.png'
import 日出 from '../../assets/日出.png'
import 雨天 from '../../assets/雨天.png'
import 旱季 from '../../assets/旱季.png'

const timeLineIcon = [晴天, 彩虹, 日出, 雨天, 旱季]
const iconNum = timeLineIcon.length

function randomIcon(){
  return timeLineIcon[ parseInt( Math.random() * iconNum ) ]
}

RecentActList.displayName = ['act-list']

export default function RecentActList(){
  let act = []
  act = useFetchRctAct()

  // 使用 useMemo 模拟 shouldcomponentUpdate, 只有依赖改变才重新计算函数的值
  let actList = useMemo(() => timeLineUI(act), [JSON.stringify(act)])

  function timeLineUI(act){
    console.log('重新渲染')
    return(
      <Timeline  lineColor='#eee'>
      {/*
        act && LazyListHoc(act, 4)(ActsCom)
      */}
        <ActsCom list={act}/>
      </Timeline>
    )
  }

  function ActsCom(props){
    let {list: act} = props
    return  (
        act.map(item=>
          <TimelineEvent 
            title={item.title}
            createdAt={item.date}
            key={item.date}
            bubbleStyle={{borderColor: '#eee'}}
            icon={<img src={randomIcon()} style={{width: '30px'}}/>}
          >
          {item.content}
          </TimelineEvent>
        )
    )
  }

  return(
    <div>
      <h4 className='column-head'
          id='act-list-head'
      >
        最近活动
      </h4>
      <div id='act-timeline'>
        {
          act && 
          actList
        }
      </div>
    </div>
  )
}