import React,{useCallback,useEffect} from 'react'
import {connect} from  'react-redux'
import {withRouter}  from 'react-router-dom'
/**自定义组件 */
import ArticleList from '../../components/common-components/articles-list'
import DiaryList from '../../components/common-components/diary-list'
import RecentActList from '../../components/common-components/recent-act-list'
import Header from '../../components/common-components/header'
/** 样式 */
import './style.css'

import { changeTab_on_mobile } from './actions'

function mapStateToProps(state){
  return{
    tab_show: state.homeReducer.tabReducer.tabName
  }
}
function mapDispatchToProps(dispatch){
  return {
    changeTab: function(tabName){
      dispatch(changeTab_on_mobile(tabName) )
    }
  }
}

export default withRouter( connect(mapStateToProps, mapDispatchToProps)(Home) )

function Home(props){
  //console.log(props)
  let {changeTab, tab_show} = props

  const tabs = [
    { key:'article-list', name:'文章',idx:1 },
    { key:'topic-list', name:'专题',idx:2},
    { key:'prj-list', name:'我的项目',idx:3},
    { key:'act-list', name:'最近活动',idx:4 },
    { key:'diary-list', name:'技术日记',idx:5 } 
  ]

  function renderColumns(){
    let components = [RecentActList, ArticleList, DiaryList]
    return(
      components.map(Com=>
        <div key={Com.displayName}
             className={
               Com.displayName.indexOf(tab_show)>-1 ?
               'show-tab':'hide-tab'
             }
        >
          <Com/>
        </div>
      )
    )
  }
  function renderTabNavOnMobile(){
    return(
      <div className='mobile-tab'>
        <div id='tabs-container'>
        {
          tabs.map(tab=>
            <div key={tab.key}
                 className='tab-nav-item'
                 id={
                   tab_show===tab.key ?
                   'tab-selected':null
                }
                 onClick={()=>changeTab(tab.key)}
            >
              {tab.name}
            </div>
          )
        }
        </div>
      </div>
    )
  }
  return(
    <div className='home-page'>
      <Header/>
      { renderTabNavOnMobile() }
      <div className='columns-act-article-diary'>
        {renderColumns()}
      </div>
    </div>
  )
}
