import axios from 'axios'

export const fetchStart = () => ({
  type: 'fetch-article-start',
})
export const fetchSuccess = (res) => ({
  type: 'fetch-article-success',
  res
})
export const fetchFailed = () => ({
  type: 'fetch-article-failed'
})

export function fetchArticleDetails(article_title){
  console.log(article_title)
  return function(dispatch, getState) {
    //console.log(dispatch)
    dispatch(fetchStart());
    function fetch(article_title){
      axios.get('/articles_json_store/'+ article_title +'.json')
      .then(data=>{
          if(data.status!=200) return dispatch( fetchFailed() )
          //console.log(data)
          let {data: article_info_obj} = data
          let {title, content, lastModified, tags} = article_info_obj
          dispatch( fetchSuccess({title, content, lastModified, tags}) )
        }
      )
    }
    fetch(article_title)
  }
}