import {useState, useEffect} from 'react'
import axios from 'axios'

export default function useFetchRctAct(){
  const [act, setAct] = useState()

  useEffect(() => {
   fetch()
  }, [])

  async function fetch(){
    axios.get('/recent_act_collection_file.json')
    .then(data=>{
      if(data.status==200){
        setAct(data.data)
      }
    })
  }

  return act
}