import React,{useMemo}  from 'react'
import prj_sepu_img from '../../my-prj-img/myprj1.png'
import './my-prj-list.css'
import {readme as sepuPrjReadme} from '../../my-prj-readme/sepu'
import marked from 'marked'
import hljs from 'highlight.js'

const testPrj = [
  {
    name: '基于 web React 的色谱软件',
    img: prj_sepu_img,
    github: 'https://github.com/qumuchegi/web-chromatography-analysis',
    readme: sepuPrjReadme
  }
]

MyPrjList.displayName = ['prj-list']
export default function MyPrjList(){

  return(
    <div id='my-prj-list'>
      {
        testPrj.map(prj=>
          <div key={prj.name} className='prj-item'>
            <h3>
              <a href={prj.github} target="_blank" rel="noopener noreferrer">{prj.name}</a>
            </h3>
            <div>
              <div className='prj-img'>
                <img src={prj.img} alt=''/>
              </div>
              <div className='prj-readme'
                   dangerouslySetInnerHTML={{
                    __html:marked(
                      prj.readme, 
                      {
                        highlight: function (code) {  //代码高亮
                            return hljs.highlightAuto(code).value
                        }
                      }
                    )
                   }}
              ></div>
            </div>
          </div>
        )
      }
    </div>
  )
}