import {combineReducers} from 'redux'

const articleFilterReducer = (state=[], action) => {
  switch(action.type){
    case 'add_article_filter':
      return [...state, action.filter_item];
    case 'remove_article_filter':
      return state.filter(item=>item!==action.filter_item);
    default: return state
  }
}
export default combineReducers({
  articleFilterReducer
})