import React,{useState, useEffect} from 'react'
import filterIcon from '../../assets/filter.png'
import './article-filter.css'

import removeIcon from '../../assets/remove.png'

import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'
import {store} from '../../store/index'

import {addArticleFilter,rmArticleFilter} from '../../store/common-actions'

const filter = {
  source:{
    title: '来源',
    options: ['原创','翻译']
  }, 
  direction: {
    title: '方向',
    options: ['前端','后端','物联网','5G','AI']
  },
  lan: {
    title: '语言',
    options: ['javascript','TypeScript','Node','C++','Python']
  },
  library: {
    title: '框架',
    options: [
      'React',
      'Electron',
      'Vue',
      'Express',
      'Nest.js',
      'Django',
      'React Router',
      'Redux',
      'Vuex',
      'Vue router',
    ]
  },
  tools: {
    title: '工具',
    options: [
      'Webpack','git','Docker','linux'
    ]
  }
}

const mapDispatchToProps = (dispatch) => ({
  addArticleFilter: (filterItem) => {
    dispatch(addArticleFilter(filterItem))
  },
  rmArticleFilter: (filterItem) => {
    dispatch(rmArticleFilter(filterItem))
  }
})

export default withRouter( connect(null, mapDispatchToProps)(ArticleFilter) )

function ArticleFilter(props){
  const [showFilter, setShowFilter] = useState(false)
  const [filter_selected, setFilter_selected] = useState([])
  
  useEffect(() => {
    let { getState } = store
    let filter_selected = getState().commonReducers. articleFilterReducer
    setFilter_selected(filter_selected)
  }, [])

  function addFilterItem(filterItem){
    props.addArticleFilter(filterItem)
    console.log('store state', store.getState())
    
    setFilter_selected(
      filter_selected=>[...new Set([...filter_selected, filterItem])]
    )
  }

  function rmFilteritem(filterItem){
    props.rmArticleFilter(filterItem)
    setFilter_selected(
      filter_selected=>filter_selected.filter(item=>item!==filterItem)
    )
  }

  function renderFilterOptions(options){
    return(
      options.map(
        item=>
        <div key={item}
             className={filter_selected.some(e=>e===item)?'selected-option':null}
             onClick={()=>addFilterItem(item)}
        >
          {item}
        </div>
      )
    )
  }

  return(
    <div id='filter-container'>
      <div id='filter-head'>
        <img src={filterIcon}
             alt=''
             className='show-filter-button'
             onClick={()=>setShowFilter(showFilter=>!showFilter)}
        />
        <div className='filter-selected'>
          {
            filter_selected.map(
              item=>
              <div key={item} className='selected-filter-item'>
                {item}
                <div className='rm-button'>
                  <img src={removeIcon}
                       onClick={()=>rmFilteritem(item)}
                  />
                </div>
              </div>
            )
          }
        </div>
      </div>
     
      <div className={showFilter?'show-filter':'hide-filter'}>
        {
          Object.keys(filter).map(k=>
            <div className='filter-condition' key={k}>
              <div>{filter[k].title}</div>
              <div>
                {
                  renderFilterOptions(filter[k].options)
                }
              </div>
            </div>
          )
        }
      </div>
    </div>
  )
}