import React,{useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import {connect} from  'react-redux'

import {store} from '../../store'
import { changeTab_on_mobile } from '../../pages/home/actions'

import { useFetchArticles } from '../custmon-hooks/useFetchArticles'
import ArticleFilter from './article-filter'
import LazyListHoc from './LazyList'
import TopicList from './topic-list'
import MyPrjList from './my-prj-list'

import { Label } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'

import tag_react from '../../assets/React.png'
import tag_vue from '../../assets/Vue.png'
import tag_docker from '../../assets/Docker.png'
import tag_node from '../../assets/node.png'
import tag_js from '../../assets/javascript.png'
import tag_selfCreate from '../../assets/creator.png'
import tag_translate from '../../assets/translate.png'
import tag_python from '../../assets/python.png'
import No from '../../assets/NO.png'

import './articles-list.css'
import './column-head.css'

const tags = {
  React: tag_react,
  Vue: tag_vue,
  Docker: tag_docker,
  javascript: tag_js,
  Node: tag_node,
  Python: tag_python,
  原创: tag_selfCreate,
  翻译: tag_translate
}
const tabTypes = ['article-list', 'topic-list','prj-list']

ArticleList.displayName = ['topic-list', 'article-list','prj-list']

export default connect(
  function mapStateToProps(state){
    return {
      tabType: state.homeReducer.tabReducer.tabName,
      article_tag: state.commonReducers.articleFilterReducer,
      filters_init: state.commonReducers.articleFilterReducer
    }
  },null
)(ArticleList) 

function ArticleList(props){
  let {dispatch} = store
  let {tabType, article_tag, filters_init} = props

  let articles = useFetchArticles(article_tag)

  useEffect(() => {
    console.log({props})
    let node = document.getElementById('article-list-head')
    let listenDomPos = function(){
      let top = node.offsetTop
      if(top!==0){
        node.style.borderColor = '#1296db'
        node.style.color = 'white'
        node.style.boxShadow = '4px 6px 10px #aaa'
        node.style.transition = 'background-color .5s, color .5s, box-shadow .5s'
      }else{
        node.style.backgroundColor = 'white'
        node.style.boxShadow = ''
        node.style.color = 'black'
      }
    }
    document.addEventListener('scroll', listenDomPos)

    return ()=>{
      document.removeEventListener('scroll', listenDomPos)
    }
  }, [])

  function matchTagImg(tag){
    return tags[tag]
  }

  function articleListCom(props){
    let {list: articles_arr} = props
    return(
      articles_arr.map(
        article=>
        <div key={article.id} 
             className='article-item'
        >
          <div className='img-title'>
            <div className='title-date'>
              <Link to={'/article/'+article.title}>
                <h4 className='title'>{article.title}</h4>
              </Link>
              <div className='last-modified'>{article.lastModified}</div>
            </div>
            <div className="img-container">
              <img src={article.tumbnail} className='tumbnail' alt=""/>
            </div>
          </div>
          <div className='tags'>
            {
              article.tags.map(
                tag=>
                <div key={tag} className='tag-item'
                >
                  <Label as='a' 
                         image 
                         size='mini'>
                    <img src={matchTagImg(tag) || No} alt=''/>
                    {tag}
                  </Label>
                </div>
              )
            }
          </div>
        </div>
      )
    )
  }

  function onChangeTab(selectedTab){
    dispatch(changeTab_on_mobile(selectedTab))
    window.scrollTo(0,0)
  }

  function tabNav(){
    return(
      <div className='tab-nav-article'>
        <div onClick={()=>onChangeTab(tabTypes[0])} 
             className={
               tabType===tabTypes[0] ? 
               'selected-tab-article'
               :'not-selected-tab-article'
            }>
               文章</div>
        <div onClick={()=>onChangeTab(tabTypes[1])}
             className={
               tabType===tabTypes[1] ? 
               'selected-tab-article'
               :'not-selected-tab-article'
            }
        >专题</div>
        <div onClick={()=>onChangeTab(tabTypes[2])}
             className={
               tabType===tabTypes[2] ? 
               'selected-tab-article'
               :'not-selected-tab-article'
            }
        >我的项目</div>
      </div>
    )
  }

 
  return (
    <div>
      <h4 className='column-head'
          id='article-list-head'
      >
      { tabNav() }
      </h4>
      <div className={ 
        tabType===tabTypes[0] ? 'show-tab-article':'hide-tab-article'}>
        <ArticleFilter/>
        <div className='articles-list'>
          {
            LazyListHoc(articles, 6)(articleListCom)
          }
        </div>
      </div>
      <div className={ 
        tabType===tabTypes[1] ? 'show-tab-article':'hide-tab-article'}>
        <TopicList/>
      </div>
      <div className={ 
        tabType===tabTypes[2] ? 'show-tab-article':'hide-tab-article'}>
        <MyPrjList/>
      </div>
    </div>
  )
}
