import React from 'react'
import useFetchTopics from '../custmon-hooks/useFetchTopics'

import {Link} from 'react-router-dom'

import './topic-list.css'

function Item(item,index){
  return(
    <div key={item.topicTitle} className='topic-item'>
      <h2 className='topic-title'>{item.topicTitle}</h2>
      <div>
        <div className='sub-topic-description'>
          {item.description}
        </div>
        <div  className='sub-topic-container'>
          <div className='sub-line'></div>
          <div>
            {
              item.articleArr.map(
                e=>
                <div className='sub-topic-item' key={e.title}>
                  <Link to={`/topic-article-details/${item.topicTitle}/${e.title}`} className='sub-topic-title'>{e.title}</Link>
                  <div className='sub-topic-time'>{e.createTime}</div>
                </div>
              )
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default function TopicList(){
  let topics = useFetchTopics()
  
  return(
    <div>
      <div className='topic-line'></div>
      {
        topics.map((item,index)=>Item(item,index))
      }
    </div>
  )
}