import React from 'react'
import Home from '../pages/home'
import Article from '../pages/article-details'
import TopicArticle from '../pages/topic-article-details'
import { matchRoutes, renderRoutes } from "react-router-config"
import NoMatch from '../pages/NoMatch'

const routes = [
  {
    component: Root,
    routes: [
      {
        path: '/',
        component: Home,
        exact: true
      },
      {
        path: '/article/:articleTitle',
        component: Article,
      },
      {
        path: '/topic-article-details/:topic/:title',
        component: TopicArticle,
      },
      {
        component: NoMatch
      }
    ]
  }
]

function Root({route}) {
  return(
    <div>
      {renderRoutes(route.routes)}
    </div>
  )
}

export default routes
