import {useEffect, useState} from 'react'
import axios from 'axios'

export default function useFetchTopics(topic){
  const [topics, setTopics] = useState([])

  useEffect(() => {
    fetch()
  }, [])

  async function fetch(){
    console.log(topic)
    axios.get('/topic_collection.json')
    .then(data=>{
      if(data.status==200){
        let topics = [...data.data]
        if(topic){
          topics = topics.filter(topicItem=>topicItem.topicTitle==topic)
        }
        console.log(topics)
        setTopics(topics)
      }
    })
  }

  return topics
}