import {useState, useEffect} from 'react'
import axios from 'axios'

export default function useFetchDiaryContent(date){
  const [content, setContent] = useState('')

  useEffect(() => {
   fetch()
  }, [date])

  async function fetch(){
    if(date!==-1 && date)
    axios.get('/diary_json_store/'+date+'.json')
    .then(data=>{
      if(data.status==200){
        console.log('获取日记内容', data.data)
        setContent(data.data)
      }
    })
  }

  return content
}