import React,{useEffect, useState} from 'react'
import {withRouter, Link} from 'react-router-dom'

import Header from '../../components/common-components/header'
import useFetchTopics from '../../components/custmon-hooks/useFetchTopics'

// 无后台评论系统
import Valine from 'valine';

import {
  Divider, 
  Loader, 
  Image, 
  Dimmer, 
  Segment} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'

import axios from 'axios'

import marked from 'marked'
import hljs from 'highlight.js'

import paragraph from '../../assets/paragraph.png'

import './style.css'
import '../article-details/code-style.css'
import '../article-details/content-style.css'

export default withRouter(
  function(props){
    const [loading, setLoading] = useState(true)
    const [article, setArticle] = useState(null)

    let {topic, title} = props.match.params

    useEffect(() => {
      function fetch(topic, title){
        axios.get('/topics_json_store/'+topic+'/'+title+'.json')
        .then(data=>{
          if(data.status===200){
            setLoading(false)
            setArticle(data.data)
            renderContent(data.data)
          }
        })
      }
      fetch(topic, title)
      window.scrollTo(0,0)
      return()=>{
        setLoading(true)
        if( document.getElementById('article-content').hasChildNodes() ){
          document.getElementById('article-content').innerHTML=null
        }
      }
    }, [title,topic])

    useEffect(() => {
      new Valine({
        el:'#tpc-article-vcomments',
        appId: '8qaiegzP4E14qhu5VrOL8yhX-gzGzoHsz',
        appKey: 'tAbdPMGf21F1afgAAkB23LNX',
        path: window.location.pathname,
        placeholder: '尽情发炎吧',
        notify: true,
        avatar: 'mp',
        pageSize: 20,
        visitor: true,
        recordIP: true,
    })
    }, [])

    function renderContent(article){
      document.getElementById('article-content').innerHTML = marked(
        article.content,
        {
          highlight: function (code) {  //代码高亮
              return hljs.highlightAuto(code).value
          }
        }
      )
    }

    return(
      <div id='topic-article-details'>
        <div id='header'>
          <Header/>
        </div>
        <div id='content-body'>
          <SidedrMenu topic={topic} title_selected={title}/>
          <div id='article-info'>
            <h1 id='title'>{article&&article.title}</h1>
            {
              loading ?
              <div id='loading'>
                <Segment>
                  <Dimmer active inverted>
                    <Loader inverted content='记载中...' />
                  </Dimmer>
                  <Image src={paragraph} />
                </Segment>
              </div>
              :null
            }
            <div id='article-content'></div>
            <Divider horizontal className='divider'>评论</Divider>
            <div id='tpc-article-vcomments'></div>
          </div>
        </div>
      </div>
    )
  }
)

function SidedrMenu({topic,title_selected}){
  let this_topic = useFetchTopics(topic)[0]
  if(this_topic){
    let {topicTitle, description, articleArr} = this_topic
    console.log({topicTitle, description, articleArr})
    return render(topicTitle, description, articleArr)
  }else return null
 
  function render(topicTitle,description,articleArr){
    return (
      <div id='sider-menu'>
        <h2>{topicTitle}</h2>
        <div>
          {
          articleArr.map(article=>
              <div key={article.title} className={title_selected===article.title ? 'topic-article-link-selected':'topic-article-link' }>
                <Link to={`/topic-article-details/${topicTitle}/${article.title}`}>{article.title}</Link>
              </div>
            )
          }
        </div>
      </div>
    )
  } 
}
