import React,{useState, useMemo} from 'react'

import useFetchDiaries  from '../custmon-hooks/useFetchDiaries'
import useFetchDiaryContent from '../custmon-hooks/useFetchDiaryContent'

import './diary-list.css'
import './column-head.css'

import Calendar from 'react-github-contribution-calendar'
import { Accordion, Icon, Rating } from 'semantic-ui-react'

import paragraph from '../../assets/paragraph.png'

var until = new Date().toLocaleDateString()
.replace(/\//g,'-')

DiaryList.displayName = ['diary-list']

var panelColors = [
  '#EEEEEE',
  '#11dddb',
  '#11ccdb',
  '#11bcdb',
  '#11aadb',
  '#1296db'
]

export default function DiaryList(){
  const [activeIndex, setActiveIndex] = useState(null)

  const diaries = useFetchDiaries() // 每次组件更新都会执行
  let diaryContent = useFetchDiaryContent(activeIndex) // 每次组件更新都会执行

  let values = {}

  for(let diaryItem of diaries){
    //values['2020-01-02'] = 5
    let [y,m,d] = diaryItem.date.split('-')
    if(m.length<2) m = '0'+m
    if(d.length<2) d = '0'+d

    values[y+'-'+m+'-'+d] = diaryItem.rate
  }
  //console.log('日历',values)
  function onChangeIdx(e, titleProps){
    diaryContent=null
    const { index } = titleProps
    const newIndex = activeIndex === index ? -1 : index

    setActiveIndex(newIndex) // 每次调用都会驱动组件更新
  }
  return(
    <div>
      <h4 className='column-head'
          id='diary-list-head'
      >技术日记</h4>
      <div id='diary-calendar'>
        <Calendar 
          panelColors = {panelColors}
          values={values} 
          until={until} 
          />
      </div>
      {
        diaries.map(diary=>
          <Accordion key={diary.date}>
            <Accordion.Title
              active={activeIndex === diary.date}
              index={diary.date}
              onClick={onChangeIdx}
            >
              <Icon name='dropdown' />
              {diary.date}
              <Rating 
                icon='star' 
                size='mini'
                disabled
                maxRating={5} 
                rating={diary.rate} 
              />
            </Accordion.Title>
            <Accordion.Content active={activeIndex === diary.date}>
              <p>
               {
               diaryContent?
               diaryContent.content
               :
               <img src={paragraph} 
                    alt=''
                    style={{width: '100px'}}
              />
              }
              </p>
            </Accordion.Content>
          </Accordion>
        )
      }
    </div>
  )
}